import {get, post, getImage, postImg } from '@/utils/request';
import {baseURL} from './index';

export const login = params => {
    return get(`${baseURL}/admin/authorize`, params).then(
        res => res.data
    );
};

export const logout = params => {
    return get(`${baseURL}/admin/logout`, params).then(
        res => res.data
    );
};

//创建管理员
export const createAdmin = params => {
    return get(`${baseURL}/admin/create`, params).then(
        res => res.data
    );
};

//用户管理
export const userInfo = params => {
    return get(`${baseURL}/user/findUser`, params).then(
        res => res.data
    );
};

//用户管理
export const userDetailInfo = params => {
    return get(`${baseURL}/user/details`, params).then(
        res => res.data
    );
};


