const state = {
  orders: [],
}

const mutations = {
  SELECTED_ORDER: (state, order) => {
    state.orders = [];
    state.orders.push(order);
    console.log(state.orders)
  },
}

const actions = {
  selectedOrder({ commit }, order) {
    console.log(order)
    commit('SELECTED_ORDER', order)
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}



