const state = {
    phones: {},
  }
  
  const mutations = {
    SELECTED_PHONE: (state, phone) => {
      state.phones = phone;
      console.log(state.phones)
    },
  }
  
  const actions = {
    selectedPhone({ commit }, phone) {
      console.log(phone)
      commit('SELECTED_PHONE', phone)
    },
  }
  
  export default {
    namespaced: true,
    state,
    mutations,
    actions
  }
  


