import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Layout from "@/layout";

const originalPush = Router.prototype.push
Router.prototype.push = function push(location, onResolve, onReject) {
if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
  return originalPush.call(this, location).catch(err => err)
}

export const constantRoutes = [
  {
    path: '/redirect',
    component: Layout,
    hidden: true,
    children: [
      {
        path: '/redirect/:path(.*)',
        component: () => import('@/views/redirect/index')
      }
    ]
  },
  {
    path: '/login',
    component: () => import('@/views/login/index'),
    hidden: true
  },
  {
    path: '/',
    component: Layout,
    redirect: '/user-manage',
    meta: { title: '用户管理', icon: 'users'},
    children: [
      {
        path: 'user-manage',
        component: () => import('@/views/user/user-manage'),
        name: '用户管理',
        meta: { title: '用户管理'},
      },
      {
        path: 'user-detail',
        component: () => import('@/views/user/user-detail'),
        name: '用户详情',
        hidden: true,
        meta: { title: '用户详情' },
      }
    ]
  },
  {
    path: '/order',
    component: Layout,
    redirect: '/order/order-manage',
    meta: { title: '订单管理', icon: 'order'},
    children: [
      {
        path: 'order-manage',
        component: () => import('@/views/order/order-manage'),
        name: '订单管理',
        meta: { title: '订单管理'},
      },
      {
        path: 'order-detail',
        component: () => import('@/views/order/order-detail'),
        name: '订单详情',
        hidden: true,
        meta: { title: '订单详情', keepAlive: false},
      }
    ]
  },
  {
    path: '/phone',
    component: Layout,
    redirect: '/phone/phone-manage',
    meta: { title: '云机管理', icon: 'phone'},
    children: [
      {
        path: 'phone-manage',
        component: () => import('@/views/phone/phone-manage'),
        name: '云机管理',
        meta: { title: '云机管理'},
      },
      {
        path: 'phone-detail',
        component: () => import('@/views/phone/phone-detail'),
        name: '云机详情',
        hidden: true,
        meta: { title: '云机详情',  keepAlive: false},
      }
    ]
  },
  {
    path: '/resources',
    component: Layout,
    redirect: '/resources/app-manage',
    meta: { title: '资源管理', icon: 'resources'},
    children: [
      {
        path: 'app-manage',
        component: () => import('@/views/resources/app-manage'),
        name: '应用管理',
        meta: { title: '应用管理'},
      },
      {
        path: 'file-manage',
        component: () => import('@/views/resources/file-manage'),
        name: '文件管理',
        meta: { title: '文件管理' },
      }
    ]
  },
  {
    path: '/statistics',
    component: Layout,
    redirect: '/statistics/user-statistics',
    meta: { title: '数据统计', icon: 'statistics'},
    children: [
      {
        path: 'user-statistics',
        component: () => import('@/views/statistics/user-statistics'),
        name: '用户统计',
        meta: { title: '用户统计'},
      },
      {
        path: 'order-statistics',
        component: () => import('@/views/statistics/order-statistics'),
        name: '订单统计',
        meta: { title: '订单统计' },
      },
      {
        path: 'phone-statistics',
        component: () => import('@/views/statistics/phone-statistics'),
        name: '云机数据',
        meta: { title: '云机数据' },
      },
      {
        path: 'phone-monitor',
        component: () => import('@/views/statistics/phone-monitor'),
        name: '云机监控',
        meta: { title: '云机监控' },
      },
      {
        path: 'user-behavior',
        component: () => import('@/views/statistics/user-behavior'),
        name: '用户行为',
        meta: { title: '用户行为' },
      }
    ]
  }
];

export const asyncRoutes = [];

if (process.env.NODE_ENV === "development") {
  var createRouter = () => new Router({
    // mode: 'history', // require service support
    routes: constantRoutes
  });
}else{
  var createRouter = () => new Router({
    base:'/mogucloudmanage/',
    // mode: 'history', // require service support
    routes: constantRoutes
  });
}


const router = createRouter();

export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher // reset router
}

export default router;


