<template>
    <div :class="classObj" class="app-wrapper">
        <SiderBar class="sidebar-container"/> 
        <div class="main-container">
            <div>
                <NavBar /> 
            </div>
            <Main />
        </div>
    </div>
</template>
<script>
import { SiderBar, NavBar, Main } from './components'
import { mapState } from 'vuex'
export default {
    name: 'Layout',
    components: {
        SiderBar,
        NavBar,
        Main
    },
    computed: {
        ...mapState({
            sidebar: state => state.app.sidebar
        }),
        classObj() {
            return {
                hideSidebar: !this.sidebar.opened,
                openSidebar: this.sidebar.opened,
            }
        }
    }
}
</script>
<style lang="less" scoped>
    @import "~@/styles/mixin.less";
    @import "~@/styles/variables.less";
    .app-wrapper {
        @include clearfix;
        position: relative;
        height: 100%;
        width: 100%;

        &.mobile.openSidebar {
        position: fixed;
        top: 0;
        }
    }

    .drawer-bg {
        background: #000;
        opacity: 0.3;
        width: 100%;
        top: 0;
        height: 100%;
        position: absolute;
        z-index: 999;
    }

    .fixed-header {
        position: fixed;
        top: 0;
        right: 0;
        z-index: 9;
        width: calc(100% - 210px);
        transition: width 0.28s;
    }

    .hideSidebar .fixed-header {
        width: calc(100% - 54px)
    }

    .mobile .fixed-header {
        width: 100%;
    }
</style>


