import axios from 'axios';
import router from '../router/index';
import domMessage from './message';

const messages = new domMessage();
axios.defaults.timeout = 10000;
axios.defaults.withCredentials=true;

// http request 拦截器
axios.interceptors.request.use(
    config => {
        return config
    },
    error => {
        return Promise.reject(error)
    }
)

// http response 拦截器
axios.interceptors.response.use(
    response => {
        if (response.data.resultCode == '-9999') {
            messages.warning({
                message: '登录过期，重新登录！',
                type: 'warning'
            });
            router.push({ path: '/', })
            return
        }
        return response
    },
    error => {
        if (error.response && error.response.status == 404) {
            router.push({ path: '/', })
        }
        return Promise.reject(error)
    }
)

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url, params = {}) {
    return new Promise((resolve, reject) => {
        axios.get(url, {
                params: params
            })
            .then(response => {
                resolve(response)
            })
            .catch(err => {
                reject(err)
            })
    })
}

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function getImage(url, params = {}) {
    return new Promise((resolve, reject) => {
        axios.get(url, { params: params, responseType: 'arraybuffer' })
            .then(response => {
                resolve(response)
            })
            .catch(err => {
                reject(err)
            })
    })
}

/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.post(url, data)
            .then(response => {
                resolve(response)
            }, err => {
                reject(err)
            })
    })
}

export function postImg(url, data) {
    return new Promise((resolve, reject) => {
        axios.post(url, data, {
                headers: {
                    'Content-Type': 'multipart/form-data;'
                },
            })
            .then(response => {
                resolve(response)
            }, err => {
                reject(err)
            })
    })
}

export function postblob(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.post(url, data)
            .then(response => {
                resolve(response.data)
            }, err => {
                reject(err)
            })
    })
}
/**
 * 封装patch请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.patch(url, data)
            .then(response => {
                resolve(response.data)
            }, err => {
                reject(err)
            })
    })
}

/**
 * 封装put请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.put(url, data)
            .then(response => {
                resolve(response.data)
            }, err => {
                reject(err)
            })
    })
}
/**
 * 文件流下载
 * @param {*} url 
 * @param {*} params 
 * @returns 
 */
 export function getBlob(url, params = {}) {
    return new Promise((resolve, reject) => {
        axios.get(url, { params: params, responseType: 'blob' })
            .then(response => {
                resolve(response)
            })
            .catch(err => {
                reject(err)
            })
    })
}


